html, body {
  height: 100%;
}

body {
  background-color: #fbeae7;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Dropdown__selector, .Dropdown__selector-items {
  width: 200px;
  font-size: 20px;;
}

.Dropdown__selector {
  background-color: white;
}

.Dropdown__selector:hover{
  background-color: wheat;
}

.Dropdown__selector-items {
  background-color: white;
}

.Dropdown__selector-items:hover {
  cursor: pointer;
  background-color: burlywood;
}

.Dropdown-open::after {
  content: "\25BC";
}

.Dropdown-close::after {
  content: "\25B2";
}

.open {
  display: block;
}

.closed {
  display: none;
}

.separate-link {
  border-bottom: 1px solid gainsboro;
  border-top: 1px solid gainsboro
}


